type Transaction = {
  id: number;
  customer_name: string;
  amount: number;
  date: string;
  status: string;
};

type Props = {
  data: Transaction[];
  sortBy: string;
  onSort: (field: "amount" | "date") => void;
};

export default function TransactionTable({ data, sortBy, onSort }: Props) {
  return (
    <table
      style={{
        width: "100%",
        borderCollapse: "collapse",
        marginTop: "16px"
      }}
      border={1}
    >
      <thead>
        <tr>
          <th>Customer</th>

          <th
            style={{ cursor: "pointer" }}
            onClick={() => onSort("amount")}
          >
            Amount {sortBy === "amount" && "▲"}
          </th>

          <th
            style={{ cursor: "pointer" }}
            onClick={() => onSort("date")}
          >
            Date {sortBy === "date" && "▲"}
          </th>

          <th>Status</th>
        </tr>
      </thead>

      <tbody>
        {data.length === 0 ? (
          <tr>
            <td colSpan={4} style={{ textAlign: "center" }}>
              No data found
            </td>
          </tr>
        ) : (
          data.map(tx => (
            <tr key={tx.id}>
              <td>{tx.customer_name}</td>
              <td>{tx.amount}</td>
              <td>{tx.date}</td>
              <td>{tx.status}</td>
            </tr>
          ))
        )}
      </tbody>
    </table>
  );
}
